 

const MovieDetail = ({ movie }) => {
  return (
    <div className="movie-detail">
      <img
        src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
        alt={movie.title}
      />
      <h1>{movie.title}</h1>
      <p>{movie.overview}</p>
      <p>평점: {movie.vote_average}</p>
    </div>
  );
};

export default MovieDetail;
